body {
    margin: 0;
    color: rgba(0,0,0,.65);
    font-size: 14px;
    font-family: Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;
    font-variant: tabular-nums;
    line-height: 1.5;
    font-feature-settings: "tnum";
}
.brand > svg {
    fill: rgb(0, 123, 255);
}
#components-layout-demo-top-side-2 .logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 28px 16px 0;
    float: left;
  }
  header{
    box-shadow: rgba(0, 0, 0, 0.02) 0px 2px 2px, rgba(0, 0, 0, 0.02) 0px 1px 0px;
  }
  
  .site-layout-background {
    background: #fff;
  }
.ant-menu-horizontal {
    line-height: 46px;
    white-space: nowrap;
    padding-left: 50%;
    border: 0;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 2px 2px, rgba(0, 0, 0, 0.02) 0px 1px 0px;
    
    
    
}
/* .ant-layout-header{
    box-shadow: rgba(0, 0, 0, 0.02) 0px 2px 2px, rgba(0, 0, 0, 0.02) 0px 1px 0px;
} */

.headerText{
    font-size: 38px;
    padding-left: 15%;
}
.content{
     margin: 0;
    padding : 7% 0% 0% 10%; 
}
small{
    font-weight: 500;
}
.ant-layout-sider-zero-width-trigger {
    position: absolute;
    top: 64px;
    right: -36px;
    z-index: 1;
    width: 36px;
    height: 42px;
    color: #fff;
    font-size: 18px;
    line-height: 42px;
    text-align: center;
    background: cadetblue !important;
    border-radius: 0 2px 2px 0;
    cursor: pointer;
    -webkit-transition: background 0.3s ease;
    transition: background 0.3s ease;
}
.form{
    margin: 2% 9% 2% 9%;
    background-color:white;
}
.navButton{
    display: none;
}
#register{
    padding: 2%;
}

/* Medium Devices */

@media only screen and (min-width: 324px) and (max-width : 767px) {
    .ant-menu-horizontal {
        line-height: 46px;
        white-space: nowrap;
        padding-left: 0%;
        border: 0;
        border-bottom: 1px solid #f0f0f0;
        -webkit-box-shadow: none;
        box-shadow: none;
    }
    
    .content{
        margin: 0;
       padding : 18% 7% 0% 12%; 
   }
    .headerText{
        font-size: 22px;
        padding-left: 0%;
    }
    .form{
        margin: 0%;
        background-color:white;
    }
    .navButton{
        display: block;
    }
}

/* Small Devices */
@media only screen and (min-width: 320px) and (max-width: 323px) {
    .ant-menu-horizontal {
        line-height: 46px;
        white-space: nowrap;
        padding-left: 0%;
        border: 0;
        border-bottom: 1px solid #f0f0f0;
        -webkit-box-shadow: none;
        box-shadow: none;
    }
    .content{
        margin: 0;
       padding : 25% 6% 0% 12%; 
   }
   
    .headerText{
        font-size: 20px;
        padding-left: 0%;
    }
    .form{
        margin: 0%;
        background-color:white;
    }
    .navButton{
        display: block;
    }
}

/* iPad */

@media only screen and (min-width: 768px) and (max-width : 1023px) {
    .ant-menu-horizontal {
        line-height: 46px;
        white-space: nowrap;
        padding-left: 28%;
        border: 0;
        border-bottom: 1px solid #f0f0f0;
        -webkit-box-shadow: none;
        box-shadow: none;
    }
    .content{
        margin: 0;
       padding : 15% 6% 0% 12%; 
   }
   
    .headerText{
        font-size: 36px;
        padding-left: 0%;
    }
    .form{
        margin: 0%;
        background-color:white;
    }
    .navButton{
        display: block;
    }
}
